@import "../../global.scss";
.portafolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;    
    
    h1{
        font-size: 50px;
        margin: 25px 0 25px;
        @include mobile{
            font-size: 30px;
        }
    }
    ul{
        margin: 10px;
        list-style: none;
        display: flex;        

        @include mobile{
            margin: 20px 0;
            flex-wrap: wrap;
            justify-content: center;
        }        
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        } 

        .item{
            width: 500px;
            height: 300px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin: 20px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }
            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
            

        }
    }
 
}